html.ecb-prototype {
    scroll-padding-top: 125px !important;
    overflow: auto;

    label.form-label {
        font-size: $h3-font-size;
        font-weight: 600;
        color: $bga-blue-black-dark;
    }

    @media (max-width: 768px) {
        scroll-padding-top: 90px !important;
    }
}

#ecb-prototype {
    overflow-x: hidden;

    .page-content {
        position: relative;
    }

    #subsite-header .bga-link-strap {
        min-height: 54px;
    }
    #subsite-header .secondary-links {
        display: block;
        a {
            display: flex;
            justify-content: flex-end;
            align-items: center;
            color: $bga-white;
            text-decoration: none;

            &:focus, &:hover {
                text-decoration: underline;
            }
        }
    }

    input {
        &.sm-width {
            max-width: 100px;
        }
    }

    .ecb-button-group {
        flex-wrap: wrap;
        row-gap: 24px;
        column-gap: 24px;

        .two-button-group {
            display: flex;
            flex-wrap: wrap;
            row-gap: 24px;
            column-gap: 24px;
        }
    }
    .save-footer {
        .button-wrapper {
            row-gap: 24px;
            column-gap: 24px;
        }
    }

    //Modals
    .modal-example {
        max-height: none;
        height: auto;
        position: fixed;
        top: 5rem;
        z-index: 9000;
        left: calc(50% - 320px);
        display: none;

        &.show {
            display: block;
        }
    }
    .modal-example, #ecb-cta-verify, #ecb-cta-verify-known {

        .icon-wrapper {
            background: $bga-green-success-dark;
            width: 32px;
            height: 32px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50px;

            img {
                width: 18px;
            }
        }

        .number-code {
            align-items: center;

            input {
                width: 56px;
                border: none;
                border-bottom: solid 4px $bga-blue-black-dark;
                font-size: 1.5rem;
                font-weight: 600;
                text-align: center;
                color: $bga-blue-black-dark;
            }

            .success-icon {
                display: none;
                position: relative;

                &.show {
                    display: flex;
                    align-items: center;
                }

                circle {
                    fill: $bga-green-success-dark;
                    stroke: $bga-green-success-dark;
                    stroke-width: 4px;
                    stroke-dasharray: 126;
                    animation: success-animation 2s linear infinite;
                    animation-timing-function: linear;
                    animation-iteration-count: 1;
                    transform: rotate(-90deg);
                    transform-origin: center;
                    position: absolute;
                    top: 0;
                    left: 0;
                }

                @keyframes success-animation {
                    0% {
                        stroke-dashoffset: 126;
                        fill: transparent;
                    }

                    50% {
                        stroke-dashoffset: 0;
                        fill: transparent;
                    }

                    100% {
                        stroke-dashoffset: 0;
                        fill: $bga-green-success-dark;
                    }
                }

                .tick {
                    position: absolute;
                    top: 20px;
                    left: 12px;
                    animation: tick-display 2s linear infinite;
                    animation-timing-function: linear;
                    animation-iteration-count: 1;
                }
                @keyframes tick-display {
                    0% {
                       opacity: 0;
                    }
                    50% {
                        opacity: 0;
                    }
                    100% {
                        opacity: 1;
                    }
                }

                .msg {
                    color: $bga-green-success-background;
                }
            }

            .error-msg {
                display: none;
            }

            &.error {
                .code-inputs {
                    box-shadow: 0 0 0 8px $bga-red-fail-mid;
                    background: $bga-red-fail-mid;
                }

                input {
                    background: $bga-red-fail-light;
                    border-bottom-color: $bga-red-fail-dark;
                }

                .error-msg {
                    display: block;

                    p {
                        color: $bga-red-fail-dark;
                    }
                }
            }
        }

        .resend {
            display: flex;

            a.sent {
                color: $bga-green-success-background;
                text-decoration: none;
                cursor: default;

                &:hover {
                    background: transparent;
                }
            }
        }

        .success-green {
            background: $bga-green-success-light;
        }
        .confirm-yellow {
            background: $bga-yellow-highlight-light;
        }
        .modal-icon-title-primary, .modal-icon-title-secondary {
            column-gap: 16px;
            align-items: center;

            svg {
                flex-shrink: 0;
                flex-grow: 0;
            }
        }
        .ecb-button-group {
            column-gap: 24px;
            row-gap: 24px;
            align-items: center;
            
            a {
                flex-shrink: 0;
                flex-grow: 0;
            }
        }
    }

    #ecb-modal-email, 
    #ecb-modal-save, 
    #ecb-cta-verify, 
    #ecb-extend-modal, 
    #ecb-modal-save-prompt, 
    #modal-save-saved-contracts,
    #modal-save-new-contract {
        .step {
            display: none;

            &.show {
                display: block;
            }
        }
    }
    #step-already-verified, #step-email-already-verified {
        .success-message {
            display: none;
            column-gap: 8px;
            
            p {
                color: $bga-green-success-dark;
                margin: 0;
            }
            
            &.show {
                display: flex;
                align-items: center;
                
            }
        }
        .success-icon {
            display: none;
            position: relative;

            &.show {
                display: flex;
                align-items: center;
                column-gap: 8px;
            }

            circle {
                fill: $bga-green-success-dark;
                stroke: $bga-green-success-dark;
                stroke-width: 4px;
                stroke-dasharray: 126;
                animation: success-animation 2s linear infinite;
                animation-timing-function: linear;
                animation-iteration-count: 1;
                transform: rotate(-90deg);
                transform-origin: center;
                position: absolute;
                top: 0;
                left: 0;
            }

            @keyframes success-animation {
                0% {
                    stroke-dashoffset: 126;
                    fill: transparent;
                }

                50% {
                    stroke-dashoffset: 0;
                    fill: transparent;
                }

                100% {
                    stroke-dashoffset: 0;
                    fill: $bga-green-success-dark;
                }
            }

            .tick {
                position: absolute;
                top: 15px;
                left: 12px;
                animation: tick-display 2s linear infinite;
                animation-timing-function: linear;
                animation-iteration-count: 1;
            }
            @keyframes tick-display {
                0% {
                   opacity: 0;
                }
                50% {
                    opacity: 0;
                }
                100% {
                    opacity: 1;
                }
            }

            .msg {
                color: $bga-green-success-background;
                animation: msg-display 2s linear infinite;
                animation-timing-function: linear;
                animation-iteration-count: 1;
            }
            @keyframes msg-display {
                0% {
                   opacity: 0;
                }
                50% {
                    opacity: 0;
                }
                100% {
                    opacity: 1;
                }
            }
        }
    }

    #email-form {
        input {
            width: auto;
            flex-grow: 1;
        }
    }

    .modal-overlay {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 8500;
        background-color: $bga-black;
        opacity: .6;
        display: none;

        &.show {
            display: block;
        }
    }

    .testing-msg {
        display: none;
        background: $bga-orange-highlight-light;
        border: $bga-orange-highlight-dark 3px double;
        font-family: 'Times New Roman', Times, serif;
        font-size: 1.2rem;
        letter-spacing: 0.1;

        strong {
            font-size: 1.6rem;
        }

        &.show {
            display: inline-block;
        }
    }

    //Sticky header 
    #page-header .include-mini-toast {
        position: relative;
    }
    #page-header.fixed {
        position: fixed;
        top: 0;
        z-index: 8500;
        width: 100%;
        box-shadow: $bga-btn-boxshadow;
        transition: top 1s linear;

        .step-title {
            display: none;
        }

        .inner-wrapper {
            padding: 0 !important;
        }

        .header-content {
            padding: 24px 32px 24px !important;
        }
    }

    .page-content.fixed {
        padding-top: 300px !important;
    }

    //Save footer
    .save-footer {
        background: $bga-yellow-highlight-mid;
    }

    // Finalise component
    .call-to-action.finalise {
        .finalise-ctas {
            row-gap: 24px;
            column-gap: 24px;
        }

        .cta-container {
            background: $bga-white;
            flex-wrap: wrap;
            width: calc(33.33% - 16px);
        }

        .cta-title {
            width: 100%;
            background: $bga-teal-highlight-mid;
            display: flex;
            column-gap: 8px;
            align-items: center;

            h3 {
                padding: 0;
            }
        }

        .cta-content {
            flex-wrap: wrap;
            height: calc(100% - 80px);
            align-content: space-between;
        }
    }

    .review-page .bga-component-wrapper {
        padding-bottom: 0 !important;

        .results-edit-answers-component {
            margin-bottom: 32px !important;
        }
    }
}
//End #ecb-prototype

.grey-bkg {
    background: $bga-blue-black-light;
}

#dummy-modal {
    background: $bga-white;
    box-shadow: $bga-btn-boxshadow;
    height: auto;

    .pretend-bga-link {
        color: $bga-blue-links;
        font-weight: 600;
        text-decoration: underline;
        font-size: 1rem;
        line-height: 1.75;
        display: inline-block;
    }

    .pretend-bga-btn {
        display: inline-flex;
        align-items: center;
        border: solid 3px $bga-yellow-highlight-shadow;
        border-radius: 100px;
        background: $bga-yellow-highlight-dark;
        color: $bga-blue-black-dark;
        font-weight: 600;
        font-size: 1rem;
        line-height: 1.5;
        padding: 8px 32px;
        min-height: 56px;
        box-shadow: $bga-btn-boxshadow;
        justify-content: center;
        text-decoration: none !important;
    }
}
#ecb-prototype.ecb-email {
    margin-top: 32px;
    body {
        background: $bga-blue-black-background-lightest;
    }
    .bkg-white {
        background: $bga-white;
    }
    .email-banner {
        flex-wrap: wrap;
    }
    .email-content {
        .code {
            display: block;
            font-weight: 700;
            color: $bga-blue-black-dark;
            font-size: 2rem;
        }
    }
    .email-banner-strap {
        background: $bga-blue-background-light;
        margin: 0 -16px;
        width: calc(100% + 32px);
        padding: 8px 16px;
    }
    .email-privacy-info {
        background: $bga-blue-black-lightest;
        margin: 0 -16px;
        width: calc(100% + 32px);
        padding: 32px 16px;
    }
    .email-footer {
        background: $bga-blue-footer;
        margin: 0 -16px;
        width: calc(100% + 32px);
        padding: 32px 16px;

        p {
            color: $bga-white;
        }

        .social-links-wrapper {
            width: 100%;
    
            .footer-social {
                display: flex;
                justify-content: center;
                margin: 0;
                padding: 0;
                flex-wrap: wrap;
                row-gap: 24px;
    
                li {
                    display: block;
                    width: 48px;
                    height: 48px;
                    background: #2E3E5A;
                    border: solid $bga-white 2px;
                    border-radius: 50%;
                    padding: 0;
                    flex-shrink: 0;
                    box-shadow: $bga-btn-boxshadow;
    
    
                    &::before {
                        display: none;
                    }
    
                    svg {
                        fill: $bga-white;
                        width: 22px;
                        height: 100%;
                        margin: auto;
                        display: block;
    
                        &.linkedin {
                            margin: -2px auto 0;
                        }
                    }
    
                    &:focus {
                        border: 3px solid $bga-white;
                        background: $bga-white;
                        outline: none;
    
                        svg {
                            fill: $bga-blue-footer;
                        }
                    }
    
                    &:hover {
                        border: solid $bga-white 3px;
                        cursor: pointer;
                        background: $bga-white;
    
                        svg {
                            fill: $bga-blue-footer;
                        }
    
                    }
                }
            }
    
        }
    }

}

.contracts-list {

    .contract {
        border-bottom: solid 1px $bga-blue-black-mid;
        padding: 24px 0;
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        column-gap: 24px;
        row-gap: 24px;
    
    }
    .contract-details {
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        row-gap: 8px;

        .contract-name {
            margin-right: 24px;
            font-size: 18px;
            font-weight: 600;
        }
    }
    .contract-actions {
        display: flex;
        justify-content: flex-end;
        column-gap: 24px;
        min-width: 330px;

        a {
            text-decoration: none;
            border-bottom: 1px solid $bga-blue-links;
            padding-bottom: 2px;
            margin-bottom: 8px;
            display: inline-flex;
            align-items: flex-end;
            line-height: 1;

            &:hover {
                cursor: pointer;
                border-bottom-color: $bga-blue-footer;

                svg path {
                    fill: $bga-blue-footer;
                }
            }
            &.inactive {
                opacity: 65%;
                cursor: default;

                &:hover {
                    background: none;
                    border-bottom: 1px solid $bga-blue-links;
                    color: $bga-blue-links;

                    svg path {
                        fill: $bga-blue-links;
                    }
                }
            }
        }
        svg {
            margin-left: 8px;
        }
    }
}
.subsite-footer .reset-links {
    background: #CA0CC1;

    .reset-links-container {
        display: flex;
        row-gap: 24px;
        column-gap: 24px;
        flex-wrap: wrap;
    }

    h4 {
        color: $bga-white;
    }
    #reset-prototype, .reset-verification {
        color: $bga-white;
        text-decoration: underline;

        &:hover {
            background: none;
        }
    }
}
.subsite-footer .saved-contracts-link {
    display: block;

    a {
        display: flex;
        align-items: center;
        color: $bga-white;
        text-decoration: none;
        font-weight: 400;

        &:focus, &:hover {
            text-decoration: underline;
            background: none;
        }
    }
}

.bga-component.mini-toast {
    display: none;
    background: $bga-white;
    background: $bga-yellow-highlight-mid;
    border: solid 1px $bga-green-success-dark;
    border: solid 1px $bga-yellow-highlight-shadow;
    border-left-width: 8px;
    width: fit-content;
    height: fit-content;
    box-shadow: $bga-btn-boxshadow;
    z-index: 9000;

    .component-content {
        display: flex;
        column-gap: 8px;
        flex-wrap: nowrap;
        align-items: center;
    }

    &.show {
        display: block;
        animation: mini-toast-animation 1s ease-in-out forwards;
        position: absolute;
        bottom: -106.5px; // To sit 24px below the header.
        right: -32px;
    }
    @keyframes mini-toast-animation {
        from {
            opacity: 0;
            bottom: -82.5px;
        }
        to {
            opacity: 1;
            bottom: -106.5px; // To sit 24px below the header.
        }
    }
}

// Media queries
@media (max-width: 1200px) {
    .contracts-list {
        .contract-details {
            flex-wrap: wrap;
        }
        .contract-name {
            width: 100%;
        }
        .expiry {
            margin-top: 8px;
        }
    }
    #ecb-prototype {
        .save-footer {
            .button-wrapper {
                justify-content: flex-start !important;
                margin-top: 24px;
            }
        }
    }
}
@media (max-width: 992px) {
    #ecb-prototype {
        #page-header.fixed {
            .header-content {
                padding: 24px 0 !important;
            }
        }

        .call-to-action.finalise {
            .finalise-ctas {
                flex-wrap: wrap;
            }

            .cta-container {
                width: 100% !important;
            }
            .cta-content {
                justify-content: space-between;
                align-items: center;
                column-gap: 16px;
            }

            .cta-title {
                width: 100%;

                svg {
                    width: 40px;
                    height: 40px;
                }
            }

            .cta-content {
                height: auto;
                display: flex;
                justify-content: space-between;
                flex-wrap: nowrap;
            

                p {
                    margin: 0 0 16px 0;

                    &:last-of-type {
                        margin: 0;
                    }
                }
                .explanation {
                    display: block;
                    flex: 1; 
                }
                .button-example {
                    flex: 0 1 auto;    
                }
            }
        }
    }
}

@media (max-width: 768px) {
    #ecb-prototype {
        #page-header.stepped-nav .stepped-navigation-wrapper {

            .stepped-navigation-wrapper {
                padding: 0 !important;
            }

            .stepped-navigation {
                padding: 16px 0 0 0 !important;

                .step {
                    width: auto;
                }
            }
        }

        #page-header.fixed {
            .header-content {
                padding: 0 !important;
            }

            .step-name {
                height: 21px;
                text-overflow: unset;
                width: auto;
            }
        }

        .page-content.fixed {
            padding-top: 60px !important;
        }

        .modal-example {
            top: 1rem;
            left: 16px;
            width: calc(100% - 32px);
            max-width: none;
        }

        .toggle-wrapper {
            padding-right: 0px !important;
        }

        .call-to-action.finalise {
            .finalise-ctas {
                flex-wrap: wrap;
            }
    
            .cta-content {
                flex-direction: column;
                align-items: flex-start !important;
                justify-content: space-between;
                flex-wrap: nowrap;
    
                row-gap: 16px;
                p {
                    margin: 0 0 16px 0;
    
                    &:last-of-type {
                        margin: 0;
                    }
                }
                .explanation {
                    display: block;
                    flex: 1; 
                }
                .button-example {
                    flex: 0 1 auto;    
                }
            }
        }
    }
    .contracts-list {
        .contract {
            flex-wrap: wrap;
        }
        .contract-actions {
            min-width: auto;
            margin-top: 16px;
            padding: 0;

            a {
                justify-content: flex-start;   
                margin-bottom: 0;
            }
        }
    }
    .bga-component.mini-toast {
        &.show {
            display: block;
            animation: mini-toast-animation 1s ease-in-out forwards;
            position: absolute;
            bottom: -82.5px; // To sit 24px below the header.
            right: -0px;
        }
        @keyframes mini-toast-animation {
            from {
                opacity: 0;
                bottom: -58.5px;
            }
            to {
                opacity: 1;
                bottom: -82.5px; // To sit 24px below the header.
            }
        }
    }
    
}

@media (max-width: 576px) {
    #ecb-prototype {
        .call-to-action.teal {
            .button-group {
                a {
                    font-size: 0.875rem;
                }
            }
        }

        .ecb-button-group a {
            font-size: 0.875rem;
        }

        #email-form {
            flex-wrap: wrap;

            input {
                width: 100%;
                margin-right: 0 !important;
            }
        }
        .modal-example .modal-icon-title-primary {
            column-gap: 8px;
            align-items: flex-start;
            
            svg {
                width: 32px;
                height: 32px;
            }
        }
       .modal-example .modal-icon-title-secondary {
            svg {
                display: none;
            }
        }
        .modal-example {
            .number-code {
                input {
                    width: 40px;
                }

                .success-icon {
                    margin-left: 8px !important;
                    svg {
                        width: 32px;
                        height: 32px;
                    }
                    circle {
                        width: 32px;
                        height: 32px;
                    }
    
                    .tick {
                        width: 14.5px;
                        height: 11.5px;
                        position: absolute;
                        top: 22px;
                        left: 8px;
                    }
                    
                    .msg {
                        display: none !important;
                    }
                }
            }
        }
        #step-already-verified, #step-email-already-verified {
            .button-group {
                column-gap: 8px;
            }
            .success-message {  
                margin-left: 8px !important;
                    svg {
                        width: 32px;
                        height: 32px;
                    }
                    circle {
                        width: 32px;
                        height: 32px;
                    }
                    .tick {
                        width: 14.5px;
                        height: 11.5px;
                        position: absolute;
                        top: 10.5px;
                        left: 8.5px;
                    }
                
                    .msg {
                        display: none !important;
                    }
            }
        }
        
    }
    .contracts-list {
        align-items: flex-start;

        .contract-actions {
            row-gap: 16px;
            flex-direction: column;
        }
    }
    
    .call-to-action.finalise {
        .cta-title {
            padding: 8px 16px !important;
        }
    }
    
}

@media (max-width: 350px) {
    #ecb-prototype {
        .call-to-action.teal {
            .button-group {
                flex-wrap: wrap;

                a {
                    font-size: 0.875rem;
                    width: 100%;
                }
            }
        }

        .modal-example {
            .button-example .bga-btn {
                width: 100%;
            }
            .resend {
                display: block;
            }
        }

        .ecb-button-group,
        .two-button-group {
            flex-wrap: wrap;

            a {
                font-size: 0.875rem;
                width: 100%;
                text-align: center
            }
        }
        
        .call-to-action.finalise {
            .cta-title {
                svg {
                    display: none;
                }
            }
        }
        .modal-example .modal-icon-title-primary {
            row-gap: 8px;
            flex-direction: column;
        }
        
    }
}