.element-state-wrapper {
    border-top: solid 1px $bga-blue-border;
}

.bga-primary-btn,
.bga-secondary-btn {
    min-width: 120px;
}

.bga-btn,
.bga-btn-state {
    display: inline-flex;
    align-items: center;
    text-align: center;
    border: solid 3px $bga-yellow-highlight-shadow;
    border-radius: 100px;
    background: $bga-yellow-highlight-dark;
    color: $bga-black;
    font-weight: 600;
    font-size: 1rem;
    line-height: 1.5;
    padding: 0 32px;
    min-height: 56px;
    box-shadow: $bga-btn-boxshadow;
    justify-content: center;
    text-decoration: none !important;

    svg {
        margin-left: 8px;
        margin-bottom: 0;
        fill: $bga-black;
        flex-shrink: 0;
    }


    &.bga-secondary-btn {
        background: $bga-white;
        border-color: $bga-blue-background-dark;
        color: $bga-black;

        &.bkg-dark {
            border-color: $bga-blue-border;
            color: $bga-black;
        }
    }
}

.bga-btn {
    &:visited, &[href]:visited {
        color: $bga-black;
    }

    &:focus, &:focus-visible {
        border-color: $bga-blue-focus;
        border-width: 3px;
        border-radius: 50px;
        background: $bga-yellow-links-background;
        color: $bga-black;
        outline: none;

        &.bga-secondary-btn {
            border-color: $bga-blue-links;

            background: $bga-blue-links;
            color: $bga-white;

            svg,
            svg path {
                fill: $bga-white;
            }
        }

        &.bkg-dark {
            background: $bga-white;
            border-color: $bga-yellow-links-background;

            &.bga-secondary-btn {
                color: $bga-white;
                background: $bga-blue-links;
                border-color: $bga-white;
            }
        }

    }

    &:hover {
        border-color: $bga-yellow-links-background;
        background: $bga-yellow-links-background;
        color: $bga-black;

        &.bga-secondary-btn {
            border-color: $bga-blue-background-dark;
            background: $bga-blue-background-dark;
            color: $bga-white;

            svg,
            svg path {
                fill: $bga-white;
            }
        }

        &.bkg-dark {
            color: $bga-black;

            &.bga-secondary-btn {
                border-color: $bga-white;
                color: $bga-white;
                background: $bga-blue-links;
            }
        }
    } 

    &:active {
        border-color: $bga-yellow-highlight-shadow;
        background: $bga-yellow-highlight-shadow;

        &.bga-secondary-btn {
            border-color: $bga-blue-footer;
            background: $bga-blue-footer;
            color: $bga-white;

            svg,
            svg path {
                fill: $bga-white;
            }
        }

        &.bkg-dark {
            border-color: $bga-yellow-highlight-shadow;
            background: $bga-yellow-highlight-shadow;

            &.bga-secondary-btn {
                border-color: $bga-white;
                background: $bga-blue-footer;
                color: $bga-white;
            }
        }
    }

    &.disabled {
        opacity: .65;
        cursor: default;
    }

    &.in-progress {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        max-width: 400px;
        
        transition: max-width 5s ease;

        .in-progress-animation { 
            display: none;
        }

        &.active {
            max-width: 448px;
            background-color: $bga-yellow-highlight-light;
            border: none;
            color: $bga-black;
            box-shadow: none;
            cursor: default;

            .in-progress-animation { 
                path {
                    fill: $bga-yellow-highlight-light;
                }
                display: block;

                &.recolour {
                    animation: fa-spin 2s linear infinite;
                    path {
                        fill: $bga-black;
                    }
                }
            }

            &.bga-secondary-btn {
                background-color: $bga-blue-background-extralight;
                color: $bga-black;

                .in-progress-animation {
                    path {
                        fill: $bga-blue-background-extralight;
                    }
                    &.recolour {
                        animation: fa-spin 2s linear infinite;
                        path {
                            fill: $bga-black;
                        }
                    }
                }
            }

            svg {
                display: none;
            }
            
       
        }
    }
}

.bga-btn-state {
    &.default {
        &.bkg-dark {
            color: $bga-black;
        }
    }
    &:visited {
        color: initial;
    }
    &.focus {
        border-color: $bga-blue-focus;
        background: $bga-yellow-links-background;
        outline: none;

        &.bga-secondary-btn {
            border-color: $bga-blue-links;
            background: $bga-blue-links;
            color: $bga-white;

            svg,
            svg path {
                fill: $bga-white;
            }
        }

        &.bkg-dark {
            background: $bga-white;
            border-color: $bga-yellow-links-background;
            color: $bga-black;

            &.bga-secondary-btn {
                color: $bga-white;
                background: $bga-blue-links;
                border-color: $bga-white;
            }
        }

    }

    &.hover {
        border-color: $bga-yellow-links-background;
        background: $bga-yellow-links-background;

        &.bga-secondary-btn {
            border-color: $bga-blue-background-dark;
            background: $bga-blue-background-dark;
            color: $bga-white;

            svg,
            svg path {
                fill: $bga-white;
            }
        }

        &.bkg-dark {
            color: $bga-black;

            &.bga-secondary-btn {
                border-color: $bga-white;
                color: $bga-white;
                background: $bga-blue-links;
            }
        }
    }

    &.active {
        border-color: $bga-yellow-highlight-shadow;
        background: $bga-yellow-highlight-shadow;

        &.bga-secondary-btn {
            border-color: $bga-blue-footer;
            background: $bga-blue-footer;
            color: $bga-white;

            svg,
            svg path {
                fill: $bga-white;
            }
        }

        &.bkg-dark {
            color: $bga-black;

            &.bga-secondary-btn {
                border-color: $bga-white;
                background: $bga-blue-footer;
                color: $bga-white;
            }
        }
    }

    &.disabled {
        opacity: .65;

        &.bkg-dark {
            color: $bga-black;
        }
    }

    &.in-progress {
        background-color: $bga-yellow-highlight-light;
        border-color: $bga-yellow-highlight-light;
        box-shadow: none;

        .in-progress-animation {
            animation: fa-spin 2s linear infinite;
        }

        &.bga-secondary-btn {
            background-color: $bga-blue-background-extralight;
            border-color: $bga-blue-background-extralight;
        }
    }
}

