.button-group {
    display: flex;
    column-gap: 24px;
    row-gap: 24px;
    align-items: center;
    flex-wrap: wrap;
}

// Modify Bootstrap buttons when they are used
.btn-primary {
    background: $bga-yellow-highlight-dark;
    border: solid 3px $bga-yellow-highlight-shadow;
    color: $bga-black;

    &:visited, &[href]:visited {
        color: $bga-black;
    }

    &:focus,
    &:focus-visible {
        background: $bga-yellow-links-background;
        border-color: $bga-blue-background-dark;
        box-shadow: none;
        color: $bga-black;
        border-radius: 50px;
        border-width: 3px;
    }
    &:visited {
        color: $bga-black;
    }

    &:hover {
        background: $bga-yellow-links-background;
        border-color: $bga-yellow-links-background;
        box-shadow: none;
        color: $bga-black;
    }

    &:active {
        background: $bga-yellow-highlight-shadow;
        border-color: $bga-yellow-highlight-shadow;
        box-shadow: none;
        color: $bga-black;
    }
}

.btn-check:checked+.btn-primary:focus,
.btn-check:active+.btn-primary:focus,
.btn-primary:active:focus,
.btn-primary.active:focus,
.show>.btn-primary.dropdown-toggle:focus {
    box-shadow: none;
}


// Mixins for smaller screen sizes and media queries
@mixin button-below576 {

    .bga-primary-btn,
    .bga-secondary-btn {
        min-width: 80px;
    }

    .bga-btn,
    button,
    .bga-btn-state {
        font-size: .875rem;
        line-height: 1.125rem;
        padding: 0px 24px;
        min-height: 48px;
    }

    .bga-link,
    .bga-link-state {
        &.bga-tertiary-link {
            font-size: .875rem;
            line-height: 1.125rem;
            min-height: 32px;
            display: flex;
            align-items: center;
        }
    }
}


// MOBILE display. Using classes instead of media queries because both breakpoint toggles and small size screens need to be taken into account.
.component-example.bp-below576, 
.component-example.bp-below350,
.component-example.bp-350 {
    @include button-below576();
}

// Media queries for HTML demo pages
@media (max-width: 576px) {
    @include button-below576();
}

@media (max-width: 350px) {
    .button-example {
        width: 100%;
    }
}